<template>
  <div class="caishi">
    <div class="list" v-for="(item, index) in caishiData" :key="index">
      <div class="item">
        <div class="title-box">
          <div class="name">{{item.name}}<van-icon name="arrow" /></div>
          <div class="img-box">
            <img class="auto-img" src="" alt="">
          </div>
        </div>
      </div>
      <div class="dish-box">
        <div class="dish" @click="searchDish(dishItem.name)" v-for="(dishItem, dishIndex) in item.cs" :key="dishIndex">{{dishItem.name}}</div>
      </div>
      
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Caishi',
    data() {
      return {
        caishiData: [],
      }
    },
    created() {
      this.getCaishiData();
    },
    methods: {
      getCaishiData() {
        this.axios({
          method: 'get',
          url: 'https://apis.netstart.cn/douguo/recipe/flatcatalogs',
        }).then(result => {
          
          if (result.status === 200) {
            this.caishiData = result.data.result.cs[1].cs;
          }
        }).catch(err => {
          
        })
      },
      searchDish(value) {
        
        this.$router.push({path: 'search', query: {value}})
      }
    }
  }
</script>

<style lang="less" scoped>
  .caishi{
    
    .item {
      margin: 10px 15px;
      width: calc(100% -30px);
      height: 50px;
      opacity: .7;
      background-color: #facc30;
      color: #2d2d2d;
      border-radius: 5px;
      .name {
        line-height: 50px;
        text-align: center;
        font-size: 15px;
        font-weight: bold;
      }
    }
  }
</style>
